html,
body {
  width: 100%;
  height: 100%;
  background-color: #f7f6f4;
}

body {
  min-width: 260px;
  font-size: 13px;
  line-height: 1.53;
  background-color: #f7f6f4;
  color: #404040;
  -webkit-font-smoothing: antialiased;
  font-family: 'Roboto-Light-webfont', Arial, sans-serif;
  font-weight: normal;
}

.out {
  position: relative;
  *zoom: 1;
}

.out:after {
  content: " ";
  display: table;
  clear: both;
}

.out_partner {
  min-height: 100vh;
  padding-bottom: 113px;
}

@media only screen and (max-width: 800px) {
  .out_partner {
    padding-bottom: 145px;
  }
}

.layout {
  max-width: 940px;
  width: 96%;
  margin: 0 auto;
}

@media only screen and (max-width: 800px) {
  .layout {
    width: 100%;
    padding: 0 20px;
  }
}

.section {
  position: relative;
  padding-top: 33px;
  *zoom: 1;
}

.section:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 800px) {
  .section {
    margin-left: -20px;
    margin-right: -20px;
  }
}

.section.mod_krn {
  overflow: hidden;
  text-align: center;
}

@media only screen and (max-width: 800px) {
  .section.mod_krn {
    padding: 33px 20px 0;
  }
}

.section__inner {
  padding: 28px 40px;
  background-color: #FFF;
  *zoom: 1;
}

.section__inner:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 800px) {
  .section__inner {
    padding: 28px 20px;
  }
}

.section__inner .assortment__box:last-of-type .assortment__item {
  border-bottom: none;
}

.section__inner .expectancy__item {
  margin: 0;
}

.section__inner .assortment__item.mod-short {
  padding-right: 0;
}

.section_side {
  padding: 1px 40px 0 0;
  background-color: #FFF;
  *zoom: 1;
}

.section_side:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 850px) {
  .section_side {
    padding: 1px 0 0;
  }
}

.section_side .search-top {
  margin-left: 30px;
}

@media only screen and (max-width: 850px) {
  .section_side .search-top {
    padding-right: 30px;
  }
}

.section__strong {
  font-size: 16px;
  line-height: 1.1;
  color: #404040;
  text-align: left;
  font-family: 'Roboto-Medium-webfont', Arial, sans-serif;
}

.section__strong.mod_dotted {
  font-size: 13px;
  display: inline-block;
  margin-top: 20px;
  border-bottom: 1px dotted #404040;
  text-transform: uppercase;
  font-family: 'ubuntum', Arial, sans-serif;
}

.section__desc {
  font-size: 15px;
  line-height: 20px;
  color: #404040;
  font-family: 'Roboto-Regular-webfont', Arial, sans-serif;
}

.section__desc strong {
  font-weight: bold;
}

.section__title {
  font-size: 38px;
  line-height: 1;
  color: #404040;
  font-family: 'Roboto-Regular-webfont', Arial, sans-serif;
}

@media only screen and (max-width: 800px) {
  .section__title {
    padding-left: 20px;
  }
}

.section__title span {
  display: inline-block;
  margin-left: 30px;
  font-size: 30px;
  color: #9a9a9a;
  font-family: 'Roboto-Light-webfont', Arial, sans-serif;
}

.section__title small {
  display: inline-block;
  font-size: 16px;
  color: #9a9a9a;
  margin-left: 30px;
  font-family: 'Roboto-Light-webfont', Arial, sans-serif;
}

@media only screen and (max-width: 800px) {
  .section__title + .section__strong {
    margin-left: 20px;
  }
}

@media only screen and (max-width: 800px) {
  .section__title + .section__desc {
    margin-left: 20px;
  }
}

.section__info {
  margin-top: 15px;
}

.section__info_desc {
  font-size: 20px;
  color: #9a9a9a;
  font-family: 'Roboto-Light-webfont', Arial, sans-serif;
}

.section__info_link {
  display: inline-block;
  font-size: 20px;
  color: #9a9a9a;
  border-bottom: 1px solid #9a9a9a;
  font-family: 'Roboto-Light-webfont', Arial, sans-serif;
  transition: 0.4s;
}

.section__info_link:hover {
  border-color: transparent;
}

.section__details img {
  float: left;
  margin: 0 20px 20px 0;
}

.section-logo {
  position: relative;
  width: 100%;
  height: 180px;
}

.section-logo img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

a.section__strong.mod_dotted {
  color: #c75858;
  border-color: #c75858;
}

a.section__strong.mod_dotted:hover {
  border-color: transparent !important;
}

a.section__strong.mod_dotted.is-checked {
  color: #7fc758;
  border-color: #7fc758;
}

.mod_pt-0 {
  padding-top: 0;
}

@media only screen and (max-width: 800px) {
  .mod_pt-0 {
    padding-top: 0;
  }
}

@media only screen and (max-width: 800px) {
  .mod_mobile {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.show-mobile {
  display: none;
}

@media only screen and (max-width: 650px) {
  .show-mobile {
    display: block;
  }
}

@media only screen and (max-width: 800px) {
  .mod_mobile-wide {
    margin-left: -20px;
    margin-right: -20px;
  }
}

.mod_last {
  margin-bottom: 40px;
}

.mod_margin-02 {
  margin: 0px 0 0px;
}

@media only screen and (max-width: 800px) {
  .mod_margin-02 {
    margin-bottom: 0px;
    padding: 35px 20px 0;
  }
}

.mod_offsetTop30 {
  margin-top: 30px;
}

.mod_offsetTop40 {
  margin-top: 40px;
}

.mod_noBg {
  background-color: transparent;
}

.mod_underline {
  padding-bottom: 1px;
  border-bottom: 1px solid #e8e8e8;
}

.el-dell, .drugstore__item.is_removed .assortment__reestablish_link {
  position: relative;
  width: 200px;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 40px;
}

@media only screen and (max-width: 650px) {
  .el-dell, .drugstore__item.is_removed .assortment__reestablish_link {
    margin-top: 20px;
  }
}

.el-dell:hover span, .drugstore__item.is_removed .assortment__reestablish_link:hover span {
  text-decoration: none;
}

.el-dell span, .drugstore__item.is_removed .assortment__reestablish_link span {
  display: inline-block;
  font-size: 15px;
  line-height: 1.1;
  color: #404040;
  text-decoration: underline;
  transition: 0.4s;
}

.el-dell .icon-remove, .drugstore__item.is_removed .assortment__reestablish_link .icon-remove {
  right: auto;
  left: 0;
}

.mod_fl {
  display: flex;
}

@media only screen and (max-width: 650px) {
  .mod_fl {
    flex-direction: column;
  }
}

.mod_fl #mapmini {
  width: 100%;
  float: none;
  margin: 0 0 10px 0;
}

.mod_fl .order__table {
  width: 100%;
  margin-bottom: 30px;
}

.mod_fl .order__table_row {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid #e8e8e8;
}

.mod_fl .order__table_cell {
  width: 115px;
  margin-right: 10px;
}

.mod_fl .order__table_cell .order__desc {
  padding: 10px 0;
}

.mod_fl .order__strong {
  display: inline-block;
}

