.bread {
	font-size: 0;
	margin-bottom: 15px;
	@include r(800) {
		padding-left: 20px; }
	&__list {
		position: relative;
		display: inline-block;
		padding-right: 10px;
		margin-right: 10px;
		font-size: 12px;
		color: $light-gray;
		@include rr;
		&:after {
			content: '\002F';
			position: absolute;
			top: 0;
			right: -5px;
			font-size: 14px;
			color: $gray; }
		&:last-child {
			&:after {
				content: none; } } }
	&__link {
		font-size: 12px;
		color: $light-gray;
		display: inline-block;
		line-height: 1.1;
		border-bottom: 1px solid $light-gray;
		@include rr;
		@include tr(.4s);
		&:hover {
			border-color: $n; } } }
// --------------------------------------
.promotion {
	@include clr;
	&__win {
		position: relative;
		width: 230px;
		float: left;
		background-color: #ffffed;
		@include r(600) {
			width: 100%; }
		&.day_win {
			&:after {
				content: '';
				position: absolute;
				top: -4px;
				left: -4px;
				@include s(icon-day-win); } } }
	&__slider {
		position: relative;
		width: calc(100% - 250px);
		float: right;
		@include r(600) {
			width: 100%;
			margin-top: 20px; } } }

.prom {
	&__item {
		background-color: #FFF; }
	&__prev,
	&__next {
		position: absolute;
		top: 42%;
		@include hide-text;
		height: 30px;
		width: 30px;
		border-radius: 30px;
		border: 2px solid #edecea;
		background: #fff;
		left: -15px;
		z-index: 1;
		@include tr(.4s);
		@include r(800) {
			left: 0; }
		&:hover {
			// opacity: .8
			border-color: $light-gray; }
		&:focus {
			outline: none; }
		&.slick-disabled {
			opacity: 0.2; }
		&:before {
			content: "";
			@include vcenter;
			@include s(slide-prev); } }
	&__next {
		right: -15px;
		left: auto;
		@include r(800) {
			left: auto;
			right: 0; }
		&:before {
			content: "";
			display: inline-block;
			@include s(slide-next); } }
	.slick-list {
		border-radius: 8px; } }
